import React from 'react';
import { Media, Image } from 'react-bootstrap';
import { IGitHubUser } from '../../util/GitHub';

import './styles.css';

interface IGitHubProfileItemProps {
  ghuser: IGitHubUser;
}

const GitHubUserProfileItem: React.FunctionComponent<IGitHubProfileItemProps> = ({ ghuser }) => {

  return (
    <Media as="li" className="media-profile-item">
      <Image
        width={ 64 }
        height={ 64 }
        className="mr-3"
        src={ ghuser.avatar_url }
        alt={ ghuser.login }
        roundedCircle
      />
      <Media.Body>
        <a href={ ghuser.html_url } className="media-profile-link" target="_blank" rel="noopener noreferrer">
          <h5>{ ghuser.login }</h5>
          <p className="text-muted">GitHub { ghuser.type }</p>
        </a>
      </Media.Body>
    </Media>
  );
};

export default GitHubUserProfileItem;