import api from '../../services/api';

import octocatImg from '../../assets/images/octocat.svg'

export interface IGitHubUser {
  id?: number;
  login?: string;
  name?: string;
  type?: string;
  avatar_url?: string;
  html_url?: string;
  following?: number;
  followers?: number;
  public_repos?: number;
}

export const defaultGitHubUser: IGitHubUser = {
  name: "Follow Me (or Not)?",
  avatar_url: octocatImg
}


export default class GitHub {
  
  static isValidUser: boolean = false;
  static responseStatus: number;

  static async getUser(username: string) {
    let user: IGitHubUser = defaultGitHubUser;
    
    await api.get(username)
      .then( (response) => {
        user = response.data;
        this.isValidUser = true;
      })
      .catch( (error) => {
        console.error(error);
        this.responseStatus = error.response.status;
        this.isValidUser = false;
      });

    return user;
  }

  static async getFollowing(username: string) {
    let followingList: IGitHubUser[] = [];
    let page = 1; 
    
    await api.get(username + "/following?per_page=100&page=" + page)
      .then( (response) => {
        followingList = response.data;
      })
      .catch( (error) => {
        console.error(error);
      });

    return followingList;
  }

  static async getFollowers(username: string) {
    let followerList: IGitHubUser[] = [];
    let page = 1;
    
    await api.get(username + "/followers?per_page=100&page=" + page)
      .then( (response) => {
        followerList = response.data;
      })
      .catch( (error) => {
        console.error(error);
      });

    return followerList;
  }

  static getNotFollowers(following: IGitHubUser[], followers: IGitHubUser[]) {

    function comparer(otherArray: IGitHubUser[]){
      return function(current: IGitHubUser){
        return otherArray.filter(function(other){
          return other.id === current.id;
        }).length === 0;
      }
    }
    
    return following.filter(comparer(followers));
  }

} 